<template>
	<div class="post" v-if="post">
		<h1 class="post__title">{{ post.title }}</h1>
		<p class="post__body">{{ post.body }}</p>
	</div>
</template>

<script>
import axios from "axios";

export default {
	props: ["id"],
	metaInfo() {
		return {
			title: this.post.title
		}
	},
	data() {
		return {
			post: null
		}
	},
	methods: {
		async getPost(id) {
			const res = await axios.get(`${this.$store.state.baseUrl}api/posts/${id}`);
			this.post = res.data;
		}
	},
	created() {
		this.getPost(this.id);
	},
	watch: {
		"$route"() {
			this.getPost(this.id);
		}
	}

}
</script>

<style>
.post {
	@apply mx-auto p-8;
}
.post__title {
	@apply text-2xl font-extrabold text-center uppercase;
}
</style>